$colors: () !default;
$colors: map-merge(
  (
    'spruce': $spruce,
    'aspen': $aspen,
    'fir': $fir,
    'oak': $oak,
    'maple': $maple,
    'hickory': $hickory,
    'elm': $elm,
  ),
