);

$primary: $aspen;
$secondary: $wahoo;
$success: $elm;
$info: $spruce;
$warning: $hickory;
$danger: $maple;
$light: $gray-100 !default;
$dark: $gray-800 !default;

$theme-colors: ();
$theme-colors: map-merge(
  (
    'primary': $primary,
    'secondary': $white,
    'success': $success,
    'info': $info,
    'warning': $warning,
    'danger': $danger,
    'light': $light,
    'dark': $dark,
  ),
  $theme-colors
);
